        <div class="c-cont">{{$t('bod.,kis/usdt LPkis/ht LPkis,,,kis/ht LPkis,')}}</div>
        <div class="line-t c-cont">{{$t('bod., KIM,(Data Access Object),,')}}</div>
      </div>
    </div> -->
